.hall {
  margin-bottom: var(--margin-big-desktop);
  &__title {
    text-align: center;
  }
}

.champion {
float: right;

  &__text {
    max-width: 200px;
    margin: 0;
    color: #fff;
    background-color: var(--back);
    border: 1px solid var(--text);
    text-align: center;
    transition: background-color 0.3s;

    &:hover {
      transition: background-color 0.3s;
      background-color: var(--main-color);
    }

    // &:hover &-btn {
    //   transition: color 0.3s;
    //   color: var(--main-color);
    // }

    &-btn {
      min-width: 200px;
      display: inline-block;
      padding: 45px;
      color: var(--text);
      transition: color 0.9s;
    }
  }
}
