.tabs {
  // tabs-online блок

  &-online {
    display: flex;
    // flex-direction: column;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
    }
  }

   // tabs-years блок

  &-years {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;

    @include tablet {
      flex-direction: column;
    }
  }

  &__nav {
    // tabs__nav_online блок

    &_online {
      display: flex;
      flex-direction: column;
      justify-content: stretch;
      min-width: 10%;
      max-width: 14%;
      align-self: center;

      @include tablet {
        flex-direction: row;
        justify-content: center;
      }

      @include small-tablet {
        max-width: 70%;
      }

      @include mobile {
        flex-direction: column;
      }

      &-item {
        width: 100%;
        position: relative;
        display: block;
        flex-grow: 1;
        max-height: 80px;

        &:not(:last-child) {
          margin-bottom: -1px;

          @include tablet {
            margin-bottom: 0px;
          }
        }

        @include tablet {
          width: auto;
        }
      }

      &-btn {
        border: 1px solid var(--text);
        width: 100%;
        height: 100%;
        padding: 10px 20px;
        transition: background-color 0.3s ease-in-out;

        @include small-tablet {
          padding: 10px 6px;
        }

        &:hover {
          transition: background-color 0.3s ease-in-out;
          background-color: var(--main-color);
        }

        input {
          cursor: pointer;
        }

        &--active {
          background-color: var(--main-color);
        }
      }
    }

    // tabs__nav_years блок

    &_years {
      width: 9%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &-item {
        margin-right: -1px;
        background-color: var(--white-text);
        transition: background-color 0.5s;
        cursor: pointer;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;

        &.focused {
          transition: background-color 0.5s;
          border-top: solid 1px;
          border-bottom: solid 1px;
          border-left: solid 1px;
          border-right: solid 1px var(--grey-line);
          background-color: var(--grey-line);
          z-index: 10;
        }


        &:hover {
          border-top: solid 1px;
          border-bottom: solid 1px;
          border-left: solid 1px;
          transition: background-color 0.5s;
          background-color: var(--grey-line);
          z-index: 10;
        }
      }

      &-btn {
        display: block;
        padding: 10px 60px 20px 10px;
        color: var(--text);
      }
    }
  }



  &__panel {
    display: none;

    &--active {
      display: block;
      // background-color: var(--text-grey);
    }


    // tabs__panel-online блок
    &-online {
      margin-top: -68px;

      @include tablet {
        margin-top: 0;
      }
    }
  }

  &__content {

    // tabs__content-online блок
    &-online {
      width: 80%;

      @include tablet {
        width: 100%;
      }
    }
    // tabs__content-years блок
    &-years {
      background-color: var(--grey-line);
      width: 91%;
          border: solid 1px;

      @include tablet {
        width: 100%;
      }
    }
  }
}
// .tubs
//     __nav => __items
//          -item
//          -btn
//    __content
//    __body
//    __panel => __block

.tubs {
  margin-bottom: var(--margin-big-desktop);

  &-divisions {
    position: relative;
    padding: 0px 0px 20px 0px;
    max-width: 100%;
    margin: 0px auto;
  }

  &__nav {
    max-width: 100%;
    display: flex;

    &-item {
      flex: 1 1 auto;
      height: 60px;
      flex-shrink: 1;
      display: flex;
      align-items: center;
      text-transform: uppercase;
      overflow: hidden;
    }

    &-btn {
      width: 100%;
      padding: 20px;
      color: var(--text);
      transition: background-color 0.3s;

      &:hover {
        transition: background-color 0.3s;
        background-color: var(--main-color);
      }

      &.active{
        transition: background-color 0.3s;
        background-color: var(--main-color);

      }
    }

  }

  &__content {

  }

  &__panel {
    position: relative;
    padding: 20px;
    font-size: 18px;
    line-height: 24px;
    display: none;

    &.active{
      display: block;

    }

    &:before {
      content: "";
      height: 50px;
      width: auto;
      position: absolute;
      top: -50px;
      background-color: var(--main-color);
      left: 0;
    }
  }

}
